@use "../../sass/" as *;

.main_frame_card_detail {
  width: 60%;
  margin: auto;
  height: 70%;
  // background-color: red;
  border: 0.3px solid rgb(226, 226, 226);
  border-radius: 10px;

  .frame_main_img_title {
    display: flex;

    margin: auto;
    padding: 2rem;

    .frame_img {
      display: flex;
      justify-content: center;

      width: 60%;

      border: 0.5px solid gray;

      img {
        width: 100% !important;
        height: 100% !important;
        position: relative;
      }
    }

    .frame_title {
      width: 40%;
      // background-color: aqua;
      h2,
      h3 {
        padding-left: 2rem;
        padding-top: 1rem;
      }
      button {

        width: 2rem;
        height: 2rem;
        margin-left: 2rem;
        background-color: transparent;
        border:  0.2px solid  grey;
        font-size: 2rem;

        
        

      }
    }
  }

  .main_frame_list_img {
    display: flex;

    padding: 0px 2rem 2rem 2rem;

    .list_img {
      width: 15%;
      padding-left: 2px;

      img {
        width: 100%;
      }
    }
  }
}
