$body-bg:#fff;
$main-color:#ff0000;
$txt-color:#fff;
$white:#fff;
$black:#000;
$box-shadow: rgba(100,100,111,0.2) 0px 7px 29px 0px ;
$box-shadows:rgba(0, 0, 0, 0.25) 0px 0px 14px 0px;

$header-height:4rem;
$header-shrink-height:5rem;
$border-radius:30px;
$mobile-width:600px;
$tablet-width:1024px;

$font-family: "Montserrat",sans-serif;
$color-seocond-text-header: #718abb;
$color-main-text-header:#0031DF;
$button-header:#0038FE;






@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Tapestry&display=swap');






:root{
 

  --sidebar-width:300px;
  --border-radius: 15px;
  --topnav-height:110px;
  
  /* you can see value of chart */
  --transition-cubic :cubic-bezier(0.175, 0.885 ,0.32, 1.275);

  --box-shadow: rgba(149,157,165,0.2) 0px 8px 24px;

  overflow-x: unset;







 

  touch-action: pan-x pan-y;
  height: 100% 

}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}


body {
  
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 0.7px;
}


a{
  text-decoration: none;
  color: unset;
  // all:unset;
  cursor: pointer;
}
a:hover{

  color: var(--main-color);

}

button{
  cursor: pointer;
  border: 0;
  outline: 0;
  color: var(--txt-color);
}



input{
  border: 1px solid transparent;
  outline: 0;
  border-radius: 8px;
}

select{

  border: 1px solid transparent;
  outline: 0;
  border-radius: 8px;

}

input:focus {
  border: 1px solid var(--color-twitter);
}

textarea{
  border: 1px solid transparent;
  outline: 0;
  overflow: hidden;
  outline: none;
  resize: none;
}

