@use "../../sass/" as *;

.displayform {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  row-gap: 4em;
  column-gap: 4rem;

  margin-bottom: 2rem;
  margin-top: 2rem;

}
.frame_element_card:hover {
  transform: translateY(-5px);
  box-shadow: $box-shadow;
}

.frame_element_card {
  width: 400px;
  background-color: white;

  border: 0.3px  solid rgb(230, 228, 228);
  

  border-radius: 10px;

  .each_element {
    width: 100%;
    height: 100%;

    p {
      text-align: center;
      font-size: larger;
    }

    img {
      width: 100%;
      height: 80%;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
  }
}




